<template>
  <v-container class="mycontainer">
    <v-layout row wrap v-if="isLoading">
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
            <br><br>
            <v-progress-circular indeterminate :size="100" color="amber"></v-progress-circular>
        </v-flex>
    </v-layout>
    <v-layout row wrap v-if="!isLoading">
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
            <v-card>
                <v-card-title>
                    <div>
                        <span class="primaryText" v-html="verseText"></span> <br><br>
                        <span class="secondaryText" v-html="sanskritVerseText"></span>
                    </div>
                </v-card-title>
            </v-card>

            <v-btn class="fixedButton" color="error" dark medium fab @click="nextVerse()">
                <v-icon>arrow_forward</v-icon>
            </v-btn>
        </v-flex>

        <v-flex xs12 sm4 offset-sm4>
        <br><br>
        <v-btn color="gray" flat @click="previousVerse()"><v-icon>arrow_back</v-icon>Back</v-btn>
        <v-btn color="gray" flat @click="changeChapter()"><v-icon>list</v-icon> Change Adhyay / Chapter</v-btn>
        <v-btn color="gray" flat to="/Languages"><v-icon>build</v-icon> Change Language</v-btn>
        <v-btn color="gray" flat @click="reset"><v-icon>refresh</v-icon> Reset</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  computed: {
    verseText () {
      return this.$store.getters.verseText
    },
    sanskritVerseText () {
      return this.$store.getters.sanskritVerseText
    },
    isLoading () {
      console.log('loading', this.$store.getters.isLoading)
      return this.$store.getters.isLoading
    }
  },

  methods: {
    nextVerse () {
      this.$store.dispatch('incrementVerseIndex')
    },
    previousVerse () {
      this.$store.dispatch('decrementVerseIndex')
    },
    reset () {
      this.$store.dispatch('resetState')
      this.$router.push({
        path: '/'
      })
      this.$router.reload()
    },
    changeChapter () {
      this.$router.push({path: `Chapters`})
    }
  }
}
</script>
<style>
    .fixedButton {
        position: fixed;
        bottom: 20%;
        right: 30px;
    }
    .primaryText {
        font-family: 'Arya', sans-serif;
        font-size: x-large;
        color: #212121;
    }
    .secondaryText {
        font-family: 'Karma', serif;
        font-size: x-large;
        color: #757575;
    }

@media only screen and (max-width: 599px) {
    .mycontainer {
        padding: 5px !important;
    }
}
</style>



