import Vue from 'vue'
import Router from 'vue-router'
import Languages from '@/components/Languages'
import Chapters from '@/components/Chapters'
import Verse from '@/components/Verse'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Languages
    },
    {
      path: '/Languages',
      name: 'Languages',
      component: Languages
    },
    {
      path: '/Chapters',
      name: 'Chapters',
      component: Chapters
    },
    {
      path: '/Verse',
      name: 'Verse',
      component: Verse
    }
  ]
})



