<template>
  <v-container>
    <v-layout row wrap>
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
          <h2>||Bhagvad Gita||</h2>
          <h3>Select Adhyay / Chapter</h3>
        </v-flex>
        <v-flex xs12 class="text-xs-center" v-for="chapter in chapters" :key="chapter.id">
          <v-btn block large round color="error" @click="changeChapter(chapter.id)">{{chapter.name}}</v-btn>
        </v-flex>
      </v-layout>
  </v-container>
</template>
<script>
import { HTTP } from '@/common/http-common'
export default {
  data () {
    return {
      chapters: [],
      errors: []
    }
  },

  computed: {
    scriptureId () {
      return this.$store.getters.scriptureId
    },
    lang () {
      return this.$store.getters.lang
    }
  },

  mounted () {
    this.$store.dispatch('isLoading', true)
    HTTP.get('chapters', {params: { scriptureId: this.scriptureId, lang: this.lang }}).then(response => {
      this.chapters = response.data
      this.$store.dispatch('isLoading', false)
    }).catch(e => {
      this.errors.push(e)
    })
  },

  methods: {
    changeChapter (chapterId) {
      console.log('in changechapter method', chapterId)
      this.$store.dispatch('changeChapter', {chapterId: chapterId})
      this.$router.push({
        path: 'Verse'
      })
    }
  }

}
</script>



